*{
  scrollbar-color: #002d97 #ffffff;
  scrollbar-width: thin;
}
body{
    background-color: var(--Bckground);
    position: relative;
    scrollbar-color:linear-gradient(90deg,#071c4d,#002d97) !important;
}

p {
    font-family: sans-serif;
    font-family: "font";
    color: var(--qoraalo_content);
}


  
/* html{
    scroll-behavior: smooth;
}
   */
  
* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    user-select: none;
}




/* width */
::-webkit-scrollbar {
  width: 7px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #fff;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background:linear-gradient(90deg,#071c4d,#002d97) !important;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
  
.haye,
.xajiye{
