/*-----------b hedad ---------------------------------*/

section.Head{
  width: 100%;
  height: 650px;
  background-color: var(--main2);
  background:linear-gradient(90deg,#071c4d,#002d97) !important;
  position: relative;
}

div.header_nav{
  width: 100%;
  position: sticky;
  top: 0;
  z-index: 9999;
  transition: all 0.5s ease-in-out;
  background-color:transparent;
  background:linear-gradient(90deg,#071c4d,#002d97) !important;
}

div.header_nav.Cadaan{
 box-shadow: 0px 0px 20px 0px #dedddd;
  background:linear-gradient(90deg,#ffffff,#ffffff) !important;
}

div.header_nav.Cadaan nav.Nav div.nav_links ul.links_items li.items_nav{
  color: var(--qoraalo_content);
}

div.header_nav.Cadaan nav.Nav div.actions button.sing {
  color: var(--qoraalo_content);
}


nav.Nav{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: wrap row;
  padding: 0% 0px;
  height: 85px;
  position: relative;
}

nav.Nav div.logo{
  flex: 1;
  height: 70px;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: wrap row;
}

nav.Nav div.logo h2.logo_desc{
  color: var(--qoraalo_ciwaano);
  font-size: 20px;
  font-family: sans-serif;
  font-family: "font";
  text-transform: capitalize;
}

nav.Nav div.logo h2.logo_desc.cadaan_logo{
  color: var(--main2);
}

nav.Nav div.logo img {
  height: 100%;
  width: 60px;
}

nav.Nav div.nav_links{
  flex: 2;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: wrap column;
  position: relative;
}
nav.Nav div.nav_links ul.links_items {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-flow: wrap row;
  gap: 30px;
  padding: 0;
  margin: 0;
  list-style: none;
  padding: 0px 0px;
  transition: all 0.6s ease-in-out;
}

nav.Nav div.nav_links ul.links_items li.items_nav {
  height: 100%;
  color: var(--qoraalo_ciwaano);
  font-family: sans-serif;
  font-family: "font";
  font-size: 15px;
  font-weight: normal;
  padding: 10px 0px;
  cursor: pointer;
  transition: all 0.4s ease-in-out;
}

nav.Nav div.nav_links ul.links_items li.items_nav:hover{
  color: var(--main2);
}

nav.Nav div.nav_links ul.links_items li.items_nav i.i{
  font-size: 12px;
}

nav.Nav div.actions{
  flex: 1;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: wrap row;
  gap: 40px;
}

nav.Nav div.actions button.sing { 
  flex: 2;
  outline: none;
  border: none;
  background-color: transparent;
  color: var(--qoraalo_ciwaano);
  font-size: 16px;
  font-family: sans-serif;
  font-family: "font";
  transition: all 0.4s;
  cursor: pointer;
}
nav.Nav div.actions button.sing:hover{
  color: var(--main2);

}
nav.Nav div.actions .reg {
  flex: 1;
}
nav.Nav div.actions button.start {
  width: 125px;
  background-color: var(--main2);
  color: var(--qoraalo_ciwaano);
  font-size: 16px;
  font-family: sans-serif;
  font-family: "font";
  transition: all 0.4s;
  border-radius: 7px;
  height: 50px;
  outline: none;
  border: none;
  transition: all 0.4 ease-in-out;
  cursor: pointer;
}

nav.Nav div.actions button.start:hover{
  background-color: #0344db;
}

div.nav_icon_mobile{
  width: 45px;
  height: 45px;
  border-radius: 2px;
  border: solid 1.50px var(--main2);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: wrap column;
  font-size: 2em;
  color: var(--qoraalo_ciwaano);
}

div.nav_icon_mobile.cadaan_mbl{
  color: var(--main2);
}

div.nav_mobile{
  width: 100%;
  height: 0px;
  position: absolute;
  background:linear-gradient(90deg,#071c4d,#002d97) !important;
  padding: 0px 0px;
  /* transform: scale(0); */
  transition: all 0.9s ease-in-out;
  overflow: hidden;
}

div.nav_mobile.active{
  height: 650px;
  /* transform: scale(1); */
  transition: all 0.9s ease-in-out;
  padding: 20px 0px;
}

div#mobile_languge{
  position: relative;
  top: -7px;
  right: -16px;
}


