div.container_login{
  width: 100%;
  height: 100vh;
  background:linear-gradient(90deg,#071c4d,#002d97) !important;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: wrap column;
}
div.container_login div.login_card{
  width: 40%;
  background-color: var(--qoraalo_ciwaano);
  padding: 5% 3%;
  border-radius: 7px;
}

div.container_login div.login_card form.login_from{
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-flow: wrap column;
}

div.container_login div.login_card form.login_from input{
  width: 100%;
  height: 50px;
  outline: none;
  background: var(--qoraalo_ciwaano);
  border: none;
  padding-left: 15px;
  padding-right: 15px;
  margin: 10px 0px;
  border-radius: 5px;
  border: solid 1px #2466ff69;
  color: var(--qoraalo_content);
  font-size: 16px;
  font-family: sans-serif;
  font-family: "font";
  text-transform: capitalize;
  caret-color: var(--qoraalo_content);
}

div.container_login div.login_card form.login_from span{
  font-size: 18px;
  font-family: sans-serif;
  font-family: "font";
  text-transform: capitalize;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: wrap column;
  margin: 2% 0px;
}

div.container_login div.login_card form.login_from span.Massage{
  color: #ff4800;

}
div.container_login div.login_card form.login_from button.submit_btn{
  width: 55%;
  height: 45px;
  background-color: var(--main2);
  color: var(--qoraalo_ciwaano);
  font-size: 15px;
  font-family: sans-serif;
  font-family: "font";
  text-transform: capitalize;
  transition: all 0.4s ease-in-out;
  outline: none;
  border: none;
  border-radius: 5px;
  margin: 2% 0px;
  cursor: pointer;
}

div.container_login div.login_card form.login_from div.upload{
  width: 100%;
  height: 70px;
  background-color: var(--qoraalo_ciwaano);
  color: var(--main2);
  font-size: 25px;
  font-family: sans-serif;
  font-family: "font";
  text-transform: capitalize;
  transition: all 0.4s ease-in-out;
  outline: none;
  border: none;
  border-radius: 5px;
  margin: 2% 0px;
  border: dashed 1.50px var(--main2);
  text-transform: capitalize;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-flow: wrap column;
  gap: 5px;
  padding: 0px 10px;
  cursor: pointer;
}

div.container_login div.login_card form.login_from div.upload P.file_name_and_zise{
  font-size: 13px;
}

div.container_login div.login_card form.login_from button.submit_btn:hover{
background-color: #2466ff;
}


div.container_login div.login_card form.login_from input::placeholder{
  color: #2466ff69;
  font-size: 16px;
  font-family: sans-serif;
  font-family: "font";
  text-transform: capitalize;
}

